<script>
	import { getContext } from "svelte";
	import { delegateClick } from "@xbs/lib-core-dom";
	import Button from "./Button.svelte";

	export let value;
	export let current;
	export let cancel;
	export let part;

	const locale = getContext("wx-i18n").getRaw().calendar;
	const months = locale.monthShort;

	let monthNum;
	$: {
		if (part !== "normal" && value) {
			if (part === "left" && value.start)
				monthNum = value.start.getMonth();
			else if (part === "right" && value.end)
				monthNum = value.end.getMonth();
			else monthNum = current.getMonth();
		} else {
			monthNum = current.getMonth();
		}
	}

	const selectMonths = {
		click: selectMonth,
	};
	function selectMonth(month) {
		if (month || month === 0) {
			current.setMonth(month);
			current = current;
		}

		if (part === "normal") value = new Date(current);

		cancel();
	}

</script>

<div class="months" use:delegateClick={selectMonths}>
	{#each months as month, i}
		<div class="month" class:current={monthNum === i} data-id={i}>
			{month}
		</div>
	{/each}
</div>
<div class="buttons">
	<Button click={cancel}>{locale.done}</Button>
</div>

<style>
	.months {
		display: flex;
		flex-wrap: wrap;
		margin: calc(var(--wx-calendar-cell-gap) / -2);
	}

	.month {
		flex: 0 0 calc(100% / 4 - var(--wx-calendar-cell-gap));
		max-width: calc(100% / 4 - var(--wx-calendar-cell-gap));
		margin: calc(var(--wx-calendar-cell-gap) / 2 + 5px)
			calc(var(--wx-calendar-cell-gap) / 2);
		text-align: center;
		cursor: pointer;
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: center;
		height: var(--wx-calendar-cell-size);
		border-radius: calc(var(--wx-calendar-cell-size) / 2);
	}
	.month.current {
		background: var(--wx-color-primary);
		color: var(--wx-color-primary-font);
	}
	.month:not(.current):hover {
		background-color: var(--wx-background-hover);
	}

	.buttons {
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: center;
		margin-top: var(--wx-calendar-gutter);
	}

</style>
