<script>
	import { getContext } from "svelte";
	import { delegateClick } from "@xbs/lib-core-dom";
	import Button from "./Button.svelte";

	const _ = getContext("wx-i18n").getRaw().calendar;

	export let value;
	export let current;
	export let cancel;
	export let part;

	let years;
	let year;
	$: {
		year = current.getFullYear();

		const start = year - (year % 10) - 1;
		const end = start + 12;
		years = [];
		for (let y = start; y < end; ++y) {
			years.push(y);
		}
	}

	const selectYears = {
		click: selectYear,
	};
	function selectYear(year) {
		if (year) {
			current.setFullYear(year);
			current = current;
		}

		if (part === "normal") value = new Date(current);

		cancel();
	}

</script>

<div class="years" use:delegateClick={selectYears}>
	{#each years as y, i}
		<div
			class="year"
			class:current={year == y}
			class:prev-decade={i === 0}
			class:next-decade={i === 11}
			data-id={y}>
			{y}
		</div>
	{/each}
</div>
<div class="buttons">
	<Button click={cancel}>{_.done}</Button>
</div>

<style>
	.years {
		display: flex;
		flex-wrap: wrap;
		margin: calc(var(--wx-calendar-cell-gap) / -2);
	}

	.year {
		flex: 0 0 calc(100% / 4 - var(--wx-calendar-cell-gap));
		max-width: calc(100% / 4 - var(--wx-calendar-cell-gap));
		margin: calc(var(--wx-calendar-cell-gap) / 2 + 5px)
			calc(var(--wx-calendar-cell-gap) / 2);
		text-align: center;
		cursor: pointer;
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: center;
		height: var(--wx-calendar-cell-size);
		border-radius: calc(var(--wx-calendar-cell-size) / 2);
	}
	.year.current {
		background: var(--wx-color-primary);
		color: var(--wx-color-primary-font);
	}
	.year:not(.current):hover {
		background-color: var(--wx-background-hover);
	}
	.prev-decade,
	.next-decade {
		color: var(--wx-color-font-disabled);
	}
	.buttons {
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: center;
		margin-top: var(--wx-calendar-gutter);
	}

</style>
