<script>
    import { Portal } from "@xbs/svelte-wx";
    import { id } from "@xbs/lib-core-dom";
    import Menu from "./Menu.svelte";
    import { filterMenu } from "../helpers";

    import { createEventDispatcher } from "svelte";
    const dispatch = createEventDispatcher();

    const SLOTS = $$props.$$slots;

    export let options;
    export let at = "bottom";
    export let resolver = null;
    export let dataKey = "contextId";
    export let filter = null;
    export const handler = show;
    export let relativeTo = undefined;

    var filteredOptions;
    $: filteredOptions = options;

    var item = null;
    var parent = null;
    let left = 0, top = 0;
    
    function onClick(ev){
        parent = null;
        dispatch("click", ev.detail);
    }
    function getDataAttr(node, name){
        let v = null;
        while (node && node.dataset && !v){
            v = node.dataset[name];
            node = node.parentNode;
        }
        return v ? id(v) : null;
    }
    function show(ev, obj){
        if (!ev) {
            parent = null;
            return;
        }

        const target = ev.target;
        if (target && target.dataset && target.dataset.menuIgnore) return;

        left = ev.clientX+1;
        top = ev.clientY+1;

        item = (typeof obj !== "undefined" ? obj: getDataAttr(target, dataKey));
        if (resolver){
            item = resolver(item, ev);
            if (!item) return;
        }

        if (item !== null && filter){
            filteredOptions = filterMenu(options, v => filter(v, item));
        }
        parent = target;
        ev.preventDefault();
    }
</script>
    
{#if SLOTS && SLOTS.default}
<div on:contextmenu={show} data-menu-ignore="true">
    <slot />
</div>
{/if}
{#if parent}
<Portal target={relativeTo} let:mount>
    <Menu 
        {at}
        {top}
        {left}
        {mount}
        {parent}
        {relativeTo}
        object={item}
        on:click={onClick}
        options={filteredOptions}
        />
</Portal>
{/if}