<script>
	import { createEventDispatcher } from "svelte";
	import List from "./helpers/List.svelte";
	import { uid } from "@xbs/lib-core-dom";

	export let value = "";
	export let id = uid();
	export let options = [];
	export let textField = "label";
	export let placeholder = "";
	export let disabled = false;
	export let error = false;

	const dispatch = createEventDispatcher();

	let text = "";
	let selected = null;
	let filterOptions = [];

	let navigate;
	let keydown;
	function ready(ev) {
		navigate = ev.detail.navigate;
		keydown = ev.detail.keydown;
	}

	let prevValue;
	$: {
		if (prevValue != value) {
			text =
				value || value === 0
					? options.find(a => a.id === value)[textField]
					: "";
			prevValue = value;
		}
	}
	$: filterOptions = options;

	function select(ev) {
		const id = ev.detail.id;

		if (id || id === 0) {
			selected = filterOptions.find(a => a.id === id);
			text = selected[textField];
			value = selected.id;

			filterOptions = options;

			navigate(null);
			dispatch("select", { selected });
		}
	}

	function input() {
		filterOptions = text
			? options.filter(i =>
					i[textField].toLowerCase().includes(text.toLowerCase())
			  )
			: options;
		if (filterOptions.length) navigate(0);
		else navigate(null);
	}

</script>

<div class="combo" on:click={() => navigate(0)} on:keydown={e => keydown(e)}>
	<input
		{id}
		bind:value={text}
		class:error
		{disabled}
		{placeholder}
		on:input={input} />

	<i class="icon wxi-angle-down" />

	{#if !disabled}
		<List
			let:option
			items={filterOptions}
			on:ready={ready}
			on:select={select}>
			<slot {option}>{option.name}</slot>
		</List>
	{/if}
</div>

<style>
	.combo {
		position: relative;
		width: var(--wx-input-width);
	}

	input {
		display: block;
		width: 100%;
		height: var(--wx-input-height);
		outline: none;
		background: var(--wx-input-background);
		border: var(--wx-input-border);
		border-radius: var(--wx-input-border-radius);
		font-family: var(--wx-input-font-family);
		font-size: var(--wx-input-font-size);
		line-height: var(--wx-input-line-height);
		font-weight: var(--wx-input-font-weigth);
		text-align: var(--wx-input-text-align);
		color: var(--wx-input-font-color);
		padding: var(--wx-input-padding);
		padding-right: calc(
			var(--wx-input-icon-size) + var(--wx-input-icon-indent) * 2
		);
		overflow: hidden;
		text-overflow: ellipsis;
		cursor: pointer;
	}
	input:focus {
		border: var(--wx-input-border-focus);
	}
	input::placeholder {
		color: var(--wx-input-placeholder-color);
	}
	input[disabled] {
		cursor: not-allowed;
		border: var(--wx-input-border-disabled);
		color: var(--wx-color-font-disabled);
		background: var(--wx-input-background-disabled);
	}
	input[disabled]::placeholder {
		color: var(--wx-color-font-disabled);
	}
	input[disabled] ~ .icon {
		color: var(--wx-color-font-disabled);
	}
	input.error {
		border-color: var(--wx-color-danger);
		color: var(--wx-color-danger);
	}
	input.error ~ .icon {
		color: var(--wx-color-danger);
	}

	.icon {
		position: absolute;
		right: var(--wx-input-icon-indent);
		top: 50%;
		transform: translateY(-50%);
		font-size: var(--wx-input-icon-size);
		line-height: 1;
		width: var(--wx-input-icon-size);
		height: var(--wx-input-icon-size);
		display: flex;
		justify-content: center;
		align-items: center;
		pointer-events: none;
		user-select: none;
		color: var(--wx-input-icon-color);
	}
	.icon:before {
		display: block;
	}

</style>
