<script>
	import { clickOutside } from "@xbs/lib-core-dom";
	import { onMount, createEventDispatcher } from "svelte";

	import MenuItem from "./MenuItem.svelte";
	import { prepareMenuData } from "../helpers";

	const dispatch = createEventDispatcher();

	export let options;
	$: prepareMenuData(options);

	export let level = 1;

	export let left = 0;
	export let top = 0;
	export let at = "bottom";
	export let parent = null;
	export let relativeTo = document.body;
	export let mount;
	export let object = null;
	
	let width, pos;
	let atBottom, atLeft, atFit;
	$: atBottom = at.indexOf("bottom") !== -1;
	$: atLeft = at.indexOf("left") !== -1;
	$: atFit = at.indexOf("fit") !== -1;

	function positionToParent(parent) {
		if (!parent || at === "point") return;

		pos = parent.getBoundingClientRect();
		const off = relativeTo.getBoundingClientRect();
		
		left = (at === "right" ? (pos.right+(level?0:1)) : pos.left) - off.left;
		top = (atBottom ? (pos.bottom+1) : pos.top) - off.top;
		width = atFit ? (pos.width+"px") : "auto";

		left += relativeTo.scrollLeft;
		top += relativeTo.scrollTop;
	}
	$: positionToParent(parent)


	let showSub;
	let activeItem;
	let self;
	function fitToBody() {
		// [CYPRESS] [SVELTE]
		// https://tracker.webix.io/issue/SVAR-727#focus=streamItem-4-7728.0-0
		// https://github.com/sveltejs/svelte/issues/7476
		if (!self) return;

		const bodyRect = document.body.getBoundingClientRect();
		const menuRect = self.getBoundingClientRect();

		let fixLeft = atLeft;
	/*
      [FIXME]
      we can use a bit more clever logic here
      when "bottom" strategy fails, use "top"
      and only when both fails, then "fit over" the action area

      similar to right/left
      it must be appliend only for "at" axis
      second axis, will always use "fit over" 
    */
		const dy = menuRect.bottom - bodyRect.bottom;
		if (dy > 0) top -= dy;

		const dxR = menuRect.right - bodyRect.right;
		if (dxR > 0) {
			if (at !== "right") {
				left = pos.right - menuRect.width;
			} else {
				fixLeft = true;
			}
		}

		// correct left position
		// can't do it earlier, because width of menu is unknown
		if (fixLeft)
			left = (level > 1 ? 0 : pos.left) - menuRect.width;
		
		const dxL = left - bodyRect.left;
		if (dxL < 0 && level < 2){ // [FIXME] left submenu is impossible
			if (at !== "left") {
				left -= dxL;
			} else {
				left = level > 1 ? pos.width : pos.right;
			}
		}
	}

	// unfortunately svelte doesn't guarantee that afterUpdate of child component
	// will be called after onMount in the parent one, so to be sure that Portal is already
	// moved menu to the correct parent we are registering single time handler
	if (mount)
		mount(fitToBody);
	onMount(fitToBody);

	function onLeave() {
		showSub = false;
	}
	function cancel() {
		dispatch("click", { action: null });
	}

</script>

<div
	use:clickOutside={cancel}
	bind:this={self}
	class="menu {level > 1 ? 'sub' : ''}"
	style="top:{top}px;left:{left}px;width:{width}"
	data-level={level}
	on:mouseleave={onLeave}>
	{#each options as item (item.id)}
		{#if item.type === "separator"}
			<div class="separator" />
		{:else}
			<MenuItem
				{item}
				bind:showSub
				bind:activeItem
				on:click={ev => {
					if (!item.data && !ev.defaultPrevented) {
						const pack = { item:object, action:item };
						if (item.handler)
							item.handler(pack);
						dispatch('click', pack);
					}
				}} />
		{/if}
		{#if item.data && showSub === item.id}
			<svelte:self
				relativeTo={self}
				options={item.data}
				level={level + 1}
				at="right"
				parent={activeItem}
				on:click />
		{/if}
	{/each}
</div>

<style>
	.menu {
		min-width: 125px;
		display: flex;
		flex-direction: column;
		position: absolute;
		z-index: 20;
		outline: var(--wx-border-medium);
		border-radius: var(--wx-border-radius);
		background-color: var(--wx-background);
		box-shadow: var(--wx-shadow-light);
	}
	.sub {
		z-index: 30;
	}
	.separator {
		width: 100%;
		border-top: var(--wx-border-medium);
	}

</style>