<script>
import { Portal } from "@xbs/svelte-wx";
import Menu from "./Menu.svelte";
import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();

export let options;
export let at = "bottom";
export let relativeTo = undefined;

export const handler = (ev) => {
    parent = ev.target;
    ev.preventDefault();
};


var parent = null;
function onClick(ev){
    parent = null;
    dispatch("click", ev.detail);
}
function show(ev){
    const target = ev.target;
    if (!target.dataset.menuIgnore)
        parent = target;
}

</script>

<div on:click={show} data-menu-ignore="true">
    <slot />
</div>
{#if parent}
<Portal target={relativeTo} let:mount>
    <Menu 
        {at}
        {mount}
        {parent}
        {options}
        on:click={onClick}
        {relativeTo}
        />
</Portal>
{/if}